body {
  display: flex;
  justify-content: center;
  align-items: center;
}

.input-group {
  position: relative;
  margin: 20px 0;
}

html {
  font-family: Roboto, sans-serif;
  float: left;
  width: 100%;
  margin: 0px;
  padding: 0px;
}

.form-md {
  width: 100%;
  text-align: center;
  margin: 32px auto 32px auto;
}
.form-md .form-group {
  margin-left: auto;
  margin-right: auto;
  max-width: 320px;
}
.form-group {
  margin-bottom: 16px;
  position: relative;
}
input {
  position: relative;
  display: block;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.37);
  border-radius: 4px;
  background-color: transparent;
  margin: 0px auto;
  padding: 6px 4px 4px 14px;
  height: 40px;
  outline: none !important;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.85);
  transition: all 0.2s ease-in-out;
}

label {
  position: absolute;
  top: -6px;
  left: 12px;
  text-align: left;
  display: inline-block;
  padding: 0 4px;
  height: 14px;
  line-height: 14px;
  font-size: 14px;
  font-weight: 400;
  background: #fff;
  color: #6c25ff;
  margin: 0px auto;
  cursor: text;
  transition: all 0.15s ease-in-out;
}
input:hover,
input:focus {
  border: 1px solid #000;
}

input:valid + label,
input:focus + label {
  top: -6px;
  color: -#6C25FF;
  font-weight: bold;
}

*:focus {
  outline: none;
}

.gl-form-asterisk {
  background-color: inherit;
  color: #e32b2b;
  padding: 0;
  padding-left: 3px;
}
.gl-form-asterisk:after {
  content: "*";
}
